<template>
  <nav class="menu">
    <div
      class="menu__item"
      v-for="item of menuItems"
      :key="item.id"
      :class="{ menu__item__active: item.active === true }"
      @click="setActive(item)"
    >
      <p class="menu__item--p">{{ item.title }}</p>
      <img :src="item.icon" alt="" />
    </div>
  </nav>
</template>

<script setup>
import { ref, watch } from "vue";
import { useRouter, useRoute } from "vue-router"; // Импортируем useRoute
import verification_icon from "../../assets/icons/verification.svg";
import offer_icon from "../../assets/icons/document.svg";
import add_icon from "../../assets/icons/add.svg";
import select_icon from "../../assets/icons/select.svg";
import user from "../../assets/icons/user.svg";

const router = useRouter();
const route = useRoute(); // Получаем текущий маршрут

const menuItems = ref([
  {
    id: 1,
    title: "О нас",
    route: "/",
    icon: verification_icon,
    active: false,
  },
  {
    id: 2,
    title: "Оферта",
    route: "/offer",
    icon: offer_icon,
    active: false,
  },
  {
    id: 3,
    title: "Взять заказ",
    route: "/take-order",
    icon: add_icon,
    active: false,
  },
  {
    id: 4,
    title: "Оформить зявку",
    route: "/place-order",
    icon: select_icon,
    active: false,
  },
  {
    id: 5,
    title: "Регистрация/Вход",
    route: "/registration",
    icon: user,
    active: false,
  },
]);

// Функция для обновления активного элемента
const updateActiveItem = () => {
  menuItems.value = menuItems.value.map((item) => ({
    ...item,
    active: item.route === route.path, // Сравниваем путь элемента с текущим маршрутом
  }));
};

// Вызываем при монтировании компонента, чтобы установить активный элемент
updateActiveItem();

// Отслеживаем изменения маршрута
watch(
  () => route.path, // Следим за изменением пути
  () => {
    updateActiveItem(); // Обновляем активный элемент при изменении маршрута
  }
);

const setActive = (item) => {
  router.push(item.route); // Переходим на страницу, указанную в item.route
};
</script>

<style lang="scss">
@import url(../../styles/variable.css);

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background: var(--yellow);
  border-radius: 20px;
  border-bottom-left-radius: 0px;

  @media (max-width: 860px) {
    flex-direction: column;
    justify-content: start;
    align-items: start;
    border-bottom-right-radius: 0px;
  }

  &__item {
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    padding: 5px 15px;
    border-radius: 10px;
    transition: background 0.3s ease, transform 0.3s ease;
    @media (max-width: 860px) {
      justify-content: start;
      width: 100%;
    }

    &--p {
      font-size: 25px;
      margin-left: 6px;
    }

    & img {
      display: block;
      width: 20px;
    }

    &__active {
      background: var(--white);
      transform: scale(1.05);
    }
  }
}
</style>
